
import Vue from 'vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(LoadingPlugin)

export default {
  components: {
    Sticky,
    Checker, 
    XTextarea,
    CheckerItem,
    Group,
    XButton,
    PopupPicker,
    Cell,
    Toast,
    Radio,
    Grid,
    GridItem,
    Datetime,
    XInput,
    Selector
  },
  data () {
      return {
        items:{},
        position: 'default',
        showPositionValue: false,
        time1:'',
        value2: '',
        value3:'',
        formatValueFunction (val) {
          return val.replace(/\/00/g, '')
        },
        tips:'',
        bools:['否','是'],
        bools2:['没有','有过'],
        bools3:[{key:true,value:"是"},{key:false,value:"否"}],
        boolstag:{
          'q1':false,
          'q2':false,
          'q3':false
        },
        agent6:[],
        orderlist:['上下班交通','工作环境','人际关系','工作时间','薪资福利','个人发展'],
        joblist:[],
        agent7:[],
        avatar: 'data:image/jpeg;base64,'
      }
  },
  mounted (){
    // 显示
    this.$vux.loading.show({
    text: '加载中...'
    })

     //获取求职信息 
        this.$http({
            method:'POST',
            url:this.$store.state.domain+'api/XiechengJobAPI/GetApplies',
            body:{},
            headers: {"Authorization": this.$store.state.jwt.token}
            })
        .then((d) => {
                if(d.data.success){
                  var _item = d.data.content.items;
                  for(var i=0;i<_item.length;i++){
                    this.joblist.push(_item[i].jobName)// 读取岗位列表
                  }
                }else{
                  this.showPosition(d.data.message);
                  alert(d.data.message);
                }

                  //读取
                  this.$http({
                              method:'POST',
                              url:this.$store.state.domain+'api/XiechengJobAPI/GetApplicant',
                              body:{},
                              headers: {"Authorization": this.$store.state.jwt.token}
                              })
                  .then((d) => {
                          if(!d.data.success){
                            this.showPosition(d.data.message);
                            this.$vux.loading.hide();
                            return false;
                          }else{
                            this.items=d.data.content;
                            if(this.getProperty("otherInfoForCustom","question6").value!=""){
                              this.agent6=this.getProperty("otherInfoForCustom","question6").value.split(",");
                            }
                            if(this.getProperty("otherInfoForCustom","question7").value!=""){
                              this.agent7=this.getProperty("otherInfoForCustom","question7").value.split(",");
                              for(var i=0;i<this.agent7.length;i++){
                                for(var j=0;j<this.orderlist.length;j++){
                                  if(this.orderlist[j]==this.agent7[i]){
                                    this.orderlist.splice(j,1);
                                    break;
                                  }
                                }
                              }
                            }
                            this.boolstag.q1=this.getProperty("otherInfoForCustom","question1").value=='是'?true:false;
                            this.boolstag.q2=this.getProperty("otherInfoForCustom","question2").value=='是'?true:false;
                            this.boolstag.q3=this.getProperty("otherInfoForCustom","question3").value=='是'?true:false;
                          }
                          this.$vux.loading.hide();
                        }, (d) => {
                          console.log('网络繁忙！请稍候重再试');
                  });


              }, (d) => {
                this.showPosition('网络繁忙！请稍候重再试');
        });
        

    //读取头像
    this.$http({
                method:'POST',
                url:this.$store.state.domain+'api/XiechengJobAPI/GetApplicantHeadImageData',
                body:{},
                headers: {"Authorization": this.$store.state.jwt.token}
                })
    .then((d) => {
        if(d.data.content!=''){
          this.avatar=d.data.content;
        }
    }, (d) => {
            console.log('网络繁忙！请稍候重再试');
    });
  },
  watch:{
    agent6:function(val,oldVal){
      this.getProperty("otherInfoForCustom","question6").value=val.join(",");
    },
    agent7:function(val,oldVal){
      this.getProperty("otherInfoForCustom","question7").value=val.join(",");
    }
  },
  methods:{
    dateVal(_name,index){
      this.items[_name][index].endTime="至今/00";
    },
    uploadPic(res){
      var that=this;
       var file = document.getElementById('file').files[0];
        if (file) {
            
            var reader = new FileReader();
            reader.onload = function ( event ) { 
                //var txt = event.target.result;

          var canvas = document.createElement('canvas');
          var source = document.getElementById("source");
          source.src = event.target.result;
          source.onload = function () {
            var width = source.width;
            var height = source.height;
            var context = canvas.getContext('2d');
            canvas.width = width;
            canvas.height = height;
            context.drawImage(source, 0, 0, width, height, 0, 0, width, height);
            var dataUrl = canvas.toDataURL('image/jpeg', 0.2);
            var txt = dataUrl;

                that.avatar = txt;
                that.$http({
                  method:'POST',
                  url:that.$store.state.domain+'api/XiechengJobAPI/UpdateApplicantHeadImageByBase64',
                  body:{"data": txt},
                  headers: {"Authorization": that.$store.state.jwt.token}
                  })
              .then((d) => {
                      if(d.data.success){
                        //that.getProperty('profiles','avatar').value=txt;
                        that.showPosition('头像上传成功');
                      }
                    }, (d) => {
                      that.showPosition('网络繁忙！请稍候重再试');
              });

          }
                };
          }
        reader.readAsDataURL( file );
    },
    addSelectPool(val,index){//关注程序排序
      this.orderlist.splice(index,1);
      this.agent7.push(val);
    },
    removeSelectPool(val,index){//关注程序排序
      this.agent7.splice(index,1);
      this.orderlist.push(val);
    },
    //取个人信息属性值
    getProperty(property,name){
      if(this.items[property]){
        //if(property=="profiles") console.log(this.items[property].value)
        var _hasData=false;
        for(var i=0; i<this.items[property].length;i++){
            var item = this.items[property][i];
            if(item.propertyName==name){
              //console.log(item.value)
              _hasData=true;
              return item;
              break;
            }
        }
        if(!_hasData){
          var _additem={"propertyName":name,"value":''}
          this.items[property].push(_additem);
          //console.log(name)
          return _additem;
        }
      }
    },
    //取教育经历属性值
    getEdu(index){
      var _name="educationExperiences";
      if(this.items[_name].length){
        return this.items[_name][index];
      }else{
        this.addEdu(_name);
      }
    },
    addEdu(){
      var _name="educationExperiences";
      var index=this.items[_name].length;
      var _additem={"startTime":"","endTime":"","schoolName":"","majorName":"","educationLevelName":"","formsOfLearningName":"","isFinished":true}
      this.items[_name].push(_additem)
      return this.items[_name][index];
    },
    getWork(index){
      var _name="workExperiences";
      if(this.items[_name].length){
        return this.items[_name][index];
      }else{
        this.addWork(_name);
      }
    },
    addWork(){
      var _name="workExperiences";
      var index=this.items[_name].length;
      var _additem={"startTime":"","endTime":"","companyName":"","jobTitle":"","cityName":"","salaryRange":"","jobDuty":""}
      this.items[_name].push(_additem)
      return this.items[_name][index];
    },
    getFm(index){
      var _name="familyList";
      if(this.items[_name].length){
        return this.items[_name][index];
      }else{
        this.addFm(_name);
      }
    },
    addFm(){
      var _name="familyList";
     var index=this.items[_name].length;
      var _additem={"name":"","companyName":"","relation":"","workPlace":""}
      this.items[_name].push(_additem)
      return this.items[_name][index];
    },
    removeItem(name,index){
      if(confirm("确定移除当前项？")){
        this.items[name].splice(index,1);
      }
    },
    change1(val){
      val=='是'?this.boolstag.q1=true:this.boolstag.q1=false
    },
    change2(val){
      val=='是'?this.boolstag.q2=true:this.boolstag.q2=false
    },
    change3(val){
      val=='是'?this.boolstag.q3=true:this.boolstag.q3=false
    },
    showPosition (t) {
      this.showPositionValue = true
      this.tips=t;
    },
    onSubmit(ev) {
       this.$http({
                    method:'POST',
                    url:this.$store.state.domain+'api/XiechengJobAPI/UpdateApplicant',
                    body:this.items,
                    headers: {"Authorization": this.$store.state.jwt.token}
                    })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                }else{
                  //this.showPosition('更新成功');
                }
              }, (d) => {
                this.showPosition('网络繁忙！请稍候重再试');
        });

      if(this.getProperty('profiles','ApplyJob').value==""){
        this.showPosition("请选择应聘职位!");
        return false;
      };
      if(this.getProperty('profiles','Treatment').value==""){
        this.showPosition("请填写期望待遇!");
        return false;
      };
      if(this.getProperty('profiles','WordCondition').value==""){
        this.showPosition("请选择工作状态!");
        return false;
      };
      if(this.getProperty('profiles','EOD').value==""){
        this.showPosition("请选择可到职日期!");
        return false;
      };
      if(this.getProperty('profiles','Name').value==""){
        this.showPosition("请填写姓名!");
        return false;
      };
      if(this.getProperty('profiles','Birthday').value==""){
        this.showPosition("请选择出生年月!");
        return false;
      };
      if(this.getProperty('profiles','gender').value==""){
        this.showPosition("请选择性别!");
        return false;
      };
      if(this.getProperty('profiles','Weight').value==""){
        this.showPosition("请填写体重!");
        return false;
      };
      if(this.getProperty('profiles','Height').value==""){
        this.showPosition("请填写身高!");
        return false;
      };
      if(this.getProperty('profiles','WedState').value==""){
        this.showPosition("请选择婚姻状况!");
        return false;
      };
      if(this.getProperty('profiles','Healthy').value==""){
        this.showPosition("请填写健康状况!");
        return false;
      };
      if(this.getProperty('profiles','DomicilePlace').value==""){
        this.showPosition("请填写户籍地址!");
        return false;
      };
      if(this.getProperty('profiles','CertificateNumber').value==""){
        this.showPosition("请填写身份证号!");
        return false;
      };
      if(this.getProperty('profiles','ResidenceState').value==""){
        this.showPosition("请填写现居住地!");
        return false;
      };
      if(this.getProperty('profiles','NativePlace').value==""){
        this.showPosition("请填写籍贯!");
        return false;
      };
      if(this.getProperty('profiles','Mobile').value==""){
        this.showPosition("请填写手机!");
        return false;
      };
      if(this.getProperty('profiles','telephone').value==""){
        this.showPosition("请填写联系电话!");
        return false;
      };
      if(this.getProperty('profiles','email').value==""){
        this.showPosition("请填写个人邮箱!");
        return false;
      };
      if(this.items.educationExperiences.length<1){
        this.showPosition("请填写教育培训经历！");
        return false;
      };
      if(this.items.educationExperiences.length>0){
        var _eduItems=this.items.educationExperiences;
        for(var i=0;i<_eduItems.length;i++){
          if(this.getEdu(i).startTime==""||this.getEdu(i).endTime==""||this.getEdu(i).educationLevelName==""||this.getEdu(i).formsOfLearningName==""||this.getEdu(i).schoolName==""||this.getEdu(i).majorName==""||this.getEdu(i).isFinished==""){
            this.showPosition("教育培训经历填写不完整，请检查!");
            return false;
          };
          //break;
        }
        if(this.getProperty('profiles','ForeignLang').value==""){
          this.showPosition("请填写外语水平！");
          return false;
        };
        if(this.getProperty('profiles','ComputerSkill').value==""){
          this.showPosition("请填写计算机水平！");
          return false;
        };
        if(this.getProperty('profiles','OtherSkill').value==""){
          this.showPosition("请填写其他技术（如没有，请填写'无'）！");
          return false;
        };

      };
      if(this.items.workExperiences.length>0){
        var _workItems=this.items.workExperiences;
        for(var i=0;i<_workItems.length;i++){
          if(this.getWork(i).startTime==""||this.getWork(i).endTime==""||this.getWork(i).companyName==""||this.getWork(i).cityName==""||this.getWork(i).jobTitle==""||this.getWork(i).salaryRange==""||this.getWork(i).jobDuty==""||this.getWork(i).mainAchievement==""||this.getWork(i).reasonOfLeaving==""){
            this.showPosition("工作经历填写不完整，请检查!");
            return false;
          };
          break;
        }
      };
      if(this.getProperty('otherInfoForCustom','question1').value==""||this.getProperty('otherInfoForCustom','question2').value==""||this.getProperty('otherInfoForCustom','question3').value==""||this.getProperty('otherInfoForCustom','question4').value==""||this.getProperty('otherInfoForCustom','question5').value==""||this.getProperty('otherInfoForCustom','question8').value==""||this.getProperty('otherInfoForCustom','question9').value==""||this.getProperty('otherInfoForCustom','question10').value==""||this.getProperty('otherInfoForCustom','question11').value==""||this.getProperty('otherInfoForCustom','question12').value==""){
        this.showPosition("请简要回答问题(全部必填)!");
        return false;
      };
      if(this.agent6.length==0){
        this.showPosition("请选择您可以接受的班制安排");
        return false;
      }
      if(this.agent7.length<6){
        this.showPosition("择业关注程序排序未完成，请检查");
        return false;
      }
      if(this.getProperty('otherInfoForCustom','question101').value==""||this.getProperty('otherInfoForCustom','question102').value==""||this.getProperty('otherInfoForCustom','question103').value==""||this.getProperty('otherInfoForCustom','question104').value==""||this.getProperty('otherInfoForCustom','question105').value==""||this.getProperty('otherInfoForCustom','question111').value==""||this.getProperty('otherInfoForCustom','question121').value==""||this.getProperty('otherInfoForCustom','question122').value==""){
        this.showPosition("请简要回答问题(全部必填)!");
        return false;
      };
      this.showPosition('更新成功');
      
      
    }
  }
}



